<script>	
	import { data } from '../timeline.js';
	import { onMount } from 'svelte';
	import { SunIcon, ChevronUpIcon } from 'svelte-feather-icons'; 

	const isElementInViewport = (el) => {
    var rect = el.getBoundingClientRect();
    return (
      rect.top >= 0 &&
      rect.left >= 0 &&
      rect.bottom <=
        (window.innerHeight || document.documentElement.clientHeight) &&
      rect.right <= (window.innerWidth || document.documentElement.clientWidth)
    );
  }

  const callbackFunc = () => {
  	var items = document.querySelectorAll("ul li");
    for (var i = 0; i < items.length; i++) {
      if (isElementInViewport(items[i])) {
        items[i].classList.add("in-view");
      }
      else {
      	items[i].classList.remove("in-view");
      }
    }
  }

  window.addEventListener("scroll", callbackFunc);
</script>


<a class="position-fixed top-0 start-0 p-3" href="/" style="z-index: 100;">
	<img class="rounded-circle" src="/assets/favicon.png" alt="site_logo">
</a>

<a class="position-fixed top-0 end-0 p-3" href="#" style="z-index: 100;"><SunIcon size="2x" /></a>

<span><h1>Server story so far...</h1></span>

<ul use:callbackFunc>	
	{#each data as elem, i (elem.id)}
		<li id="{i}">
			<div>
				<img class="img-fluid mx-auto d-block" src={elem.src} alt={elem.altext}>
				<h3>{elem.title}</h3>
				{elem.text}
			</div>
		</li>
	{/each}
</ul>

<a class="float-end me-4" href="#top"><ChevronUpIcon size="2x"/>Back to top</a>

<style>
	a {
		text-decoration: none;
	}
	a:active, a:visited {
		text-decoration: none;
	}
	h1 {
		padding: 5px;
		text-align: center;
		margin-top: 108px;
	}
	ul, ul[class^="svelte-"] {
	  background: #fff;
	  margin: 0px;
	  padding: 25px 50px;
	  overflow-y: hidden;
	}
 	ul li {
		list-style-type: none;
		position: relative;
		width: 6px;
		margin: 0 auto;
		padding-top: 50px;
		background: #90B8DE;
	}
 	ul li:after{
		content: '';
		position: absolute;
		left: 50%;
		bottom: 0;
		transform: translateX(-50%);
		width: 30px;
		height: 30px;
		border-radius: 50%;
		background: inherit;
		z-index: 1;
		transition: background .5s ease-in-out;
	}
 	ul li div {
	  position: relative;
	  bottom: 6px;
	  width: 400px;
	  padding: 15px;
	  background-color: #fff;
	  border-bottom: 4px solid;
	  border-color: #90B8DE;
	}
 	ul li div:before {
	  content: '';
	  position: absolute;
	  bottom: -4px;
	  width: 0;
	  height: 0;
	  border-style: solid;
	  border-right-color: inherit;
	}
 	ul li:nth-child(odd) div {
	  left: 45px;
	}
 	ul li:nth-child(odd) div:before {
	  left: -16px;
	  border-width: 8px 16px 8px 0;
	  border-color: transparent #f45b69 transparent transparent;
	  border-right-color: inherit;
	}
 	ul li:nth-child(even) div {
	  left: -439px;
	}
 	ul li:nth-child(even) div:before {
	  right: -16px;
	  border-width: 8px 0 8px 16px;
	  border-color: transparent transparent transparent #F45B69;
	  border-left-color: 	inherit;
	}
	ul li:after {
  	  transition: background 0.5s ease-in-out;
	}
	ul li.in-view:after {
	  background-color: #90B8DE;
	}
	ul li div {
	  visibility: hidden;
	  opacity: 0;
	  transition: all 0.5s ease-in-out;
	}
	ul li:nth-child(odd) div {
	  transform: translate3d(200px, 0, 0);
	}
	ul li:nth-child(even) div {
	  transform: translate3d(-200px, 0, 0);
	}
	ul li.in-view div {
	  transform: none;
	  visibility: visible;
	  opacity: 1;
	}
	
	@media screen and (max-width: 900px) {
	   ul li div {
	    width: 250px;
	  }
	   ul li:nth-child(even) div {
	    left: -289px;
	    /*250+45-6*/
	  }
	}

	@media screen and (max-width: 600px) {
		ul {
			overflow: hidden;
			width: 100vw;
			padding: 25px 10px;
		}
	   ul li {
	    margin-left: 20px;
	  }
	   ul li div {
	    width: calc(100vw - 91px);
	  }
	   ul li:nth-child(even) div {
	    left: 45px;
	  }
	   ul li:nth-child(even) div::before {
	    left: -15px;
	    border-width: 8px 16px 8px 0;
	    border-color: transparent #90B8DE transparent transparent;
	  }
	}
</style>